import requestHttps from '../../../common/request.js'
export default {
	data() {
		return {
			userInfo:[]
		}
	},
	methods: {
		sign:function(){
			var that=this
			requestHttps({
				url:'/app/logout',
				method:'POST',
				data:{},
				success:res=>{
					// that.islogin(res.data)
					console.log(res.data)
					uni.showToast({
						title:res.data.data.message,
						icon:'none'
					})
					uni.setStorage({
						key:"token",
						data:''
					})
					setTimeout(function(){
						uni.switchTab({
							url:'../../index/index'
						})
					},2000)
				}
			})
		},
		// 返回上个页面
		goback:function(){
			uni.navigateBack({
				delta:1
			})
		},
	},
	onLoad() {
		var that=this
		var user
		uni.getStorage({
			key:'userInfo',
			success(e){ 
				console.log(e)
				user=e.data
			}
		})
		that.userInfo=user
	}
}
